
*,
*:after,
*:before {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  min-height: 100vh;
  color: #292256;
  background-image: linear-gradient(
    to bottom,
    #292256 0%,
    #8446cf 70%,
    #a871d6 100%
  );
 
  background-repeat: no-repeat;
  
}

.container {
  /* position: relative;
  top: 0;
  left: 0; */
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  font-family: 'Work Sans', sans-serif;
  
}
.phoneContainer,.contactsContainer {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
  background-color: #f0f0f0;
  min-width: 330px;
}
.phoneContainer{
  margin-top: 80px;
}
.contactsContainer{
  margin-bottom: 80px;
}




.star1 {
  position: absolute;
  border-radius: 50%;
  background-color: #ffffff;
  animation: twinkle 5s infinite ease-in-out;
}

.star1::after {
  height: 100%;
  width: 100%;
  transform: rotate(90deg);
  content: '';
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
}

.star1::before {
  background: radial-gradient(
    ellipse at center,
    rgba(255, 255, 255, 0.5) 0%,
    rgba(255, 255, 255, 0) 60%,
    rgba(255, 255, 255, 0) 100%
  );
  position: absolute;
  border-radius: 50%;
  content: '';
  top: -20%;
  left: -50%;
}

.star1:nth-child(3) {
  top: 4vh;
  left: 91vw;
  width: calc((1px * 11) + 3px);
  height: calc((1px * 11) / 3);
  animation-delay: 5s;
}
